* {
    box-sizing: border-box;
}

body {
    background: #f1f1f1;
}

.project-card {
    display: flex;
    flex-direction: column;
    margin: 2rem auto;
    box-shadow: 0 3px 7px -1px rgba(#000, .1);
    margin-bottom: 1.6%;
    line-height: 1.4;
    border-radius: 5px;
    overflow: hidden;
    z-index: 0;
    a {
      color: inherit;
    }
    .meta {
      position: relative;
      z-index: 0;
      height: 200px;
    }
    .photo {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-size: cover;
      background-position: center;
      transition: transform .2s;
    }
    .description {
      padding: 1rem;
      background: #fff;
      position: relative;
      z-index: 1;
      h1 {
        line-height: 1;
        margin: 0;
        font-size: 1.7rem;
      }
      .read-more {
        text-align: right;
        a {
          color: #1976d2;
          display: inline-block;
          position: relative;
          &:after {
            content: "\f061";
            font-family: FontAwesome;
            margin-left: -10px;
            opacity: 0;
            vertical-align: middle;
            transition: margin .3s, opacity .3s;
          }
  
          &:hover:after {
            margin-left: 5px;
            opacity: 1;
          }
        }
      }
    }
    p {
      position: relative;
      margin: 1rem 0 0;
      &:first-of-type {
        margin-top: 1.25rem;
        &:before {
          content: "";
          position: absolute;
          height: 5px;
          background: #1976d2;
          width: 35px;
          top: -0.75rem;
          border-radius: 3px;
        }
      }
    }
  
    @media (min-width: 640px) {
      flex-direction: row;
      max-width: 750px;
      .meta {
        flex-basis: 40%;
        height: auto;
      }
      .description {
        flex-basis: 60%;
        &:before {
          transform: skewX(-3deg);
          content: "";
          background: #fff;
          width: 30px;
          position: absolute;
          left: -10px;
          top: 0;
          bottom: 0;
          z-index: -1;
        }
      }
      &.alt {
        flex-direction: row-reverse;
        .description {
          &:before {
            left: inherit;
            right: -10px;
            transform: skew(3deg)
          }
        }
        .details {
          padding-left: 25px;
        }
      }
    }
  }